
.page{
  height: 100vh;
  display: flex;
  flex-direction: column;
  background: #EAEBEC;
}
.page>main{
  flex-grow: 1;
}
.page>main{
  min-width: 1024px;
  max-width: 1440px;
  display: flex;
  margin-top: 16px;
  margin-bottom: 16px;
  justify-content: space-between;
  padding: 0 16px;
  width: 100%;
  align-self: center;
}
#resumeEditor{
  width: 35%;
  background: #444;
}
#resumePreview{
  flex-grow: 1;
  margin-left: 16px;
  background: #777;
}



